<h2 id="线程安全概述" tabindex="-1"><a class="header-anchor" href="#线程安全概述"><span>线程安全概述</span></a></h2>
<p>线程安全是指当多个线程访问同一个对象时，如果不用考虑这些线程在运行时环境下的调度和交替执行，也不需要进行额外的同步，或者在调用方进行任何其他的协调操作，调用这个对象的行为都可以获得正确的结果。</p>
